@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;600&display=swap);
*{margin:0;padding:0;box-sizing:border-box}html{font-size:62.5%}body{font-weight:400;font-family:"Quicksand",sans-serif;font-size:16px;line-height:1.7;color:#616161;padding-bottom:2rem;background-color:#FF8A80}a:link,a:visited{color:#E53935;text-decoration:none}a:hover,a:active{color:#FF8A80}button{background-color:transparent;border:none}span{display:inline !important}.wrapper{margin-right:auto;margin-left:auto;height:100%;max-width:960px;padding-right:10px;padding-left:10px;display:flex;align-items:center;justify-content:center}.main{height:75vh;box-shadow:0px 13px 40px -13px rgba(0,0,0,0.75);width:50%;margin:0 auto;position:absolute;top:50%;left:50%;-ms-transform:translate(-50%, -50%);transform:translate(-50%, -50%);border-radius:3px;overflow:hidden}@media (max-width: 1024px){.main{height:50vh;width:80%}}@media (max-width: 768px){.main{height:50vh;width:95%}}@media (max-width: 600px){.main{height:100vh;width:100%}}.header{text-align:center;border:transparent;background-color:#E53935;padding:2rem 3rem}.header__title{font-weight:600;color:white}.content{background-color:white;width:100%;height:100%;padding:1rem 3rem}.date-time{margin-top:1rem;text-align:center;color:#E53935}.tagline{font-size:2rem;width:100%;text-align:center;margin-top:2rem;letter-spacing:0.7px}@media (max-width: 600px){.tagline{font-size:1.5rem}}.weather-search__form{text-align:center;margin-top:3rem;margin-bottom:5rem}.weather-search__input{display:inline-block;border:solid 2px transparent;background-color:rgba(229,57,53,0.1);padding:0.5rem 3rem;border-radius:2px;margin-right:2rem;padding-left:1rem;padding-right:4rem}.weather-search__input:focus{outline:none;border-bottom:solid 2px #E53935}.weather-search__submit{display:inline-block;text-align:center;background-color:transparent;font-size:3rem;height:30px;width:30px;position:relative;top:0.9rem;margin-left:-6rem}.weather-search__button{padding:1.2rem;color:#E53935;position:absolute;top:50%;left:50%;-ms-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.weather-search__button:focus{outline:none}.weather-data{text-align:center;margin-top:3rem}.weather-data__city{color:#E53935}.weather-data__box{display:flex;align-items:center;justify-content:center;margin-top:5rem}@media (max-width: 768px){.weather-data__box{margin-top:2rem}}@media (max-width: 600px){.weather-data__box{margin-top:4rem}}.weather-data__property{margin:0 3rem}@media (max-width: 400px){.weather-data__property{margin:0 2rem}}.weather-data__title{font-size:0.8rem;text-transform:uppercase;font-weight:600;letter-spacing:1px}.weather-data__value{color:#E53935;font-size:4rem;line-height:0.6}@media (max-width: 400px){.weather-data__value{font-size:3rem}}.error{margin-top:3rem;text-align:center}.footer{position:absolute;bottom:0;padding:1.5rem 0;left:50%;text-align:center;-ms-transform:translateX(-50%);transform:translateX(-50%);border-top:solid 2px rgba(255,138,128,0.3);letter-spacing:0.3px}@media (max-width: 600px){.footer{letter-spacing:0;width:90%}}

